<template>
    <ChoiceScreen>
        <a @click="back()">
            <span class="material-icons">arrow_back</span>
            Voltar
        </a>
        
        <Section name="Escolha sua Herana" />

        <div class="heritages-row">
            <div class="heritages-choice">
                <div class="heritages-card" v-for="heritage in heritagesList" :key="heritage.id" @click="getHeritageInfo(heritage.id)">
                    <h2>{{ heritage.name }}</h2>
                </div>
            </div>
            
            <div class="heritages-info" v-show="heritageSelected == true">
                <div v-for="heritage in heritageInfo" :key="heritage.id">
                    <h2>{{ heritage.name }}</h2>

                    <p><strong>Aparncia.</strong> {{ heritage.appearance }}</p>

                    <p><strong>Sociedade.</strong> {{ heritage.society }}</p>

                    <p><strong>Tamanho.</strong> {{ heritage.size }}</p>

                    <!-- <p><strong>Deslocamento.</strong> {{ heritage.move }} + <span class="attribute">Agilidade</span></p> -->

                    <button @click.prevent="heritageSave(heritage)">Escolher {{ heritage.name }}</button>
                </div>
            </div>

            <div class="heritages-info" v-show="heritageSelected == false">   
                <h2>Heranas</h2>

                <p>As Heranas so o maior presente que recebemos para Etheria, dentre as diversas criaturas que povoam o continente, algumas criaturas foram abenoadas com uma grande afinidade elemental.</p>
                <p>Escolher uma herana  como colocar uma lente, ela determina como voc v o mundo e entende as culturas a sua volta.</p>
                
                <h3>Caractersticas das Heranas</h3>

                <ul>
                    <li><strong>Habilidades.</strong> Toda herana possui um caminho de habilidades exclusivo chamado Ancestralidade. Voc sempre comea com a primeira habilidade daquele caminho e pode escolher (+2) habilidades da lista.</li>
                    <li><strong>Tamanho.</strong> Sua herana define seu tamanho. Veja mais sobre isso na pgina X.</li>
                    <li><strong>Deslocamento.</strong> Seu deslocamento define quantos metros voc anda quando usa uma ao de Mover, esse deslocamento leva em considerao somente deslocamento terrestre, outros tipos de deslocamento esto especificados na descrio de sua respectiva herana.</li>
                </ul>
            </div>
        </div>
    </ChoiceScreen>
</template>

<script>
    // Import components 
    import ChoiceScreen from '@/components/characters/ChoiceScreen.vue'
    import Section from '@/components/template/Section.vue'
    
    // Import packages
    import { mapState } from 'vuex'

    export default {
        name: 'HeritagesScreen',
        components: {
            ChoiceScreen, Section
        },
        created() {
            if (this.heritagesList.length === 0) {
                this.$store.dispatch('heritages/getHeritagesList')
            }      
        },
        computed: {
            ...mapState({
                heritageInfo: state => state.heritages.heritageInfo,
                heritageSelected: state => state.heritages.heritageSelected,
                heritagesList: state => state.heritages.heritagesList,
            })
        },
        methods: {
            back () {
                this.$router.push("/create-character")
                this.$store.dispatch('heritages/getHeritageState', false)
                document.body.style.overflow = "visible";  
            },
            getHeritageInfo(id) {
                this.$store.dispatch('heritages/getHeritageInfo', id)
                this.$store.dispatch('heritages/getHeritageState', true)
            },
            heritageSave(heritage) {
                this.$store.dispatch('heritages/getCharacterHeritage', heritage)
                this.$router.push("/create-character")
            }
        }
    }
</script>

